          </el-select>
        </el-form-item>
        <!-- ===方向选择==== -->
        <el-form-item label-width="80px" label="方向" style="margin-right: 0;margin-bottom: 18px;">
          <el-select v-model="directionData" style="width:202px">
            <el-option :label="item" :value="item" v-for="item in direction" :key="item"></el-option>
          </el-select>
        </el-form-item>

        <!-- ====录入人选择==== -->
        <el-form-item label-width="80px" label="录入人" style="margin-right: 0;margin-bottom: 18px;">
          <el-select v-model="createrData" style="width:196px">
            <el-option :label="item.username" :value="item.username" v-for="item in createrList"
              :key="item.id"></el-option>
          </el-select>
        </el-form-item>

        <!-- ====根据备注查找==== -->
        <el-form-item label-width="80px" label="题目备注" style="margin-right: 0;margin-bottom: 18px;">
          <el-input style="width:202px" v-model="remarkData"></el-input>
        </el-form-item>

        <!-- ====根据企业简称==== -->
        <el-form-item label-width="80px" label="企业简称" style="margin-right: 0;margin-bottom: 18px;">
          <el-input style="width:202px" v-model="companyData"></el-input>
        </el-form-item>

        <el-form-item label-width="80px" label="城市" style="margin-right: 154px;">

          <el-select v-model="city" style="width:100px;margin-right:2px;" @change="getAreaList">
            <el-option :label="item" :value="item" v-for="item in cityList" :key="item"></el-option>
          </el-select>

          <el-select v-model="area" style="width:100px">
            <el-option :label="item" :value="item" v-for="item in areaList" :key="item"></el-option>
          </el-select>

        </el-form-item>

        <!-- ====按钮==== -->
        <el-form-item style="margin-right: 0;">
          <!-- ==清除按钮== -->
          <el-button size="small" @click="clearForm">清除</el-button>
          <!-- ==搜索按钮== -->
          <el-button type="primary" size="small" @click="sumbit()">搜索</el-button>
        </el-form-item>

      </el-form>

      <!-- ==================================条数总数==================================-->
      <el-alert :title="'数据一共' + $store.state.questionsChoice.questions.counts + '条'" type="info" show-icon :closable="false"
        style="margin-bottom: 15px;"></el-alert>

      <!-- ==================================数据表格==================================-->
      <el-table :data="tableData" style="width: 100%"
        :header-cell-style="{ background: '#fafafa', color: '#909399', 'border-bottom': '2px solid #e8e8e8' }"
        :row-style="{ height: '67px', }">

        <el-table-column label="试题编号" min-width="120px" :cell-style="{ 'text-algin': 'left' }" align="left">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.number }}</span>
          </template>
        </el-table-column>

        <el-table-column label="学科" min-width="80px" align="left">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.subject }}</span>
          </template>
        </el-table-column>

        <el-table-column label="目录" min-width="80px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.catalog }}</span>
          </template>
        </el-table-column>

        <el-table-column label="题型" min-width="80px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{
              scope.row.questionType === '1' ? scope.row.questionType = '单选题' : scope.row.questionType === '2' ?
              scope.row.questionType = '多选题' : scope.row.questionType === '3' ? scope.row.questionType = '简答题' :
                scope.row.questionType = scope.row.questionType
            }}</span>
          </template>
        </el-table-column>

        <el-table-column label="题干" min-width="280px" align="left">
          <template slot-scope="scope">
            <div v-html="scope.row.question"></div>
            <!-- <span style="margin-left: 10px">{{ scope.row.question }}</span> -->
          </template>
        </el-table-column>

        <el-table-column label="录入时间" min-width="180px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.addDate | parseTimeByString }}</span>
          </template>
        </el-table-column>

        <el-table-column label="难度" min-width="80px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.difficulty === '1' ? scope.row.difficulty = '简单' :
              scope.row.difficulty === '2' ?
                scope.row.difficulty = '普通' : scope.row.difficulty === '3' ? scope.row.difficulty = '困难' :
                  scope.row.difficulty = scope.row.difficulty }}</span>
          </template>
        </el-table-column>

        <el-table-column label="录入人" min-width="80px">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.creator }}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作" min-width="180px">
          <!-- ======================================操作区========================== -->
          <template slot-scope="scope">
            <div class="function">
              <el-button icon="el-icon-view" circle class="view" @click="viewBox(scope.row.id)"></el-button>
              <el-button icon="el-icon-edit" circle class="edit" @click="$router.push(`new/?id=${scope.row.id}`)"></el-button>
              <el-button icon="el-icon-delete" circle class="delete" @click="deleteQuestion(scope.row.id)"></el-button>
              <el-button icon="el-icon-check" circle class="check" @click="joinChoice(scope.row.id)"></el-button>
            </div>
          </template>
        </el-table-column>

      </el-table>

      <!-- 分页 -->
      <div class="Pagination"  v-if="$store.state.questionsChoice.questions.counts>0">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageSet.page"
          :page-sizes="[5, 10, 20, 30]" :page-size="pageSet.pageSize" layout="prev, pager,next,sizes,jumper"
          :total="$store.state.questionsChoice.questions.counts" background>
        </el-pagination>
      </div>

    </el-card>
    <!-- ===========================================预览页面============================================================= -->
    <el-dialog :visible.sync="viewShow" title="题目预览" width="65%">
      <div class="questionBase">
        <span class="question-item">【题型】：{{
          QuestionDetail.questionType === '1' ? QuestionDetail.questionType = '单选题' : QuestionDetail.questionType === '2'
          ?
          QuestionDetail.questionType = '多选题' : QuestionDetail.questionType === '3' ? QuestionDetail.questionType = '简答题'
            :
            QuestionDetail.questionType = QuestionDetail.questionType
        }}</span>
        <span class="question-item">【编号】：{{ QuestionDetail.id }}</span>
        <span class="question-item">【难度】：{{
          QuestionDetail.difficulty === '1' ? QuestionDetail.difficulty = '单选题' : QuestionDetail.difficulty === '2' ?
          QuestionDetail.difficulty = '多选题' : QuestionDetail.difficulty === '3' ? QuestionDetail.difficulty = '简答题' :
            QuestionDetail.difficulty = QuestionDetail.difficulty
        }}</span>
        <span class="question-item">【标签】：{{ QuestionDetail.tags }}</span>
        <span class="question-item">【学科】：{{ QuestionDetail.subjectName }}</span>
        <span class="question-item">【目录】：{{ QuestionDetail.directoryName }}</span>
        <span class="question-item">【方向】：{{ QuestionDetail.direction }}</span>
      </div>
      <div class="questionMain">
        <p>【题干】：<span v-html="QuestionDetail.question" style="color: #0000ff;"></span></p>
        <p></p>
      </div>

      <div class="typeOne" v-if="QuestionDetail.questionType === '1' || QuestionDetail.questionType === '单选题'"
        style="border-bottom: 1px solid #9a9a9a;">
        <p>单选题 选项：（以下选中的选项为正确答案）</p>
        <el-radio-group v-model="RadioisRight">
          <div v-for="item in QuestionDetail.options" :key="item.id" style="margin-bottom: 10px;padding: 8px 0;">
            <el-radio :label="item.code" style="pointer-events: none;">{{ item.title }}</el-radio>
          </div>
        </el-radio-group>
      </div>

      <div class="typeTwo" v-if="QuestionDetail.questionType === '2' || QuestionDetail.questionType === '多选题'|| QuestionDetail.questionType === '多选'" style="border-bottom: 1px solid #9a9a9a;">
        <p>多选题 选项：（以下选中的选项为正确答案）</p>
        <el-checkbox-group v-model="checkRight">
          <div v-for="item in QuestionDetail.options" :key="item.id" style="margin-bottom: 10px;padding: 8px 0;">
          <el-checkbox :label="item.code" style="pointer-events: none;">{{ item.title }}</el-checkbox>
        </div>
        </el-checkbox-group>

        <!-- <div v-for="item in cities" :key="item" style="margin-bottom: 10px;padding: 8px 0;">
          <el-checkbox :label="item.code" :name="item.questionId" v-model="aa">{{ item }}</el-checkbox>
        </div> -->
      </div>

      <div class="videoItem" style="border-bottom: 1px solid #9a9a9a;padding: 5px 0;">
        【参考答案】：<el-button type="danger" @click="video()" size="small">视频答案预览</el-button>
        <br>
        <video :src="QuestionDetail.videoURL" v-if="videoShow" style="width: 50%;" controls autoplay></video>
      </div>

      <div class="anserItem" style="border-bottom: 1px solid #9a9a9a;padding: 10px 0;display: flex;align-items: center;">
        【答案解析】：<span v-html="QuestionDetail.answer"></span>
      </div>

      <div class="anserItem" style="padding: 20px 0;">
        【题目备注】：{{ QuestionDetail.remarks }}
        <br>
      </div>
      <div style="display: flex;justify-content: end;"><el-button type="primary" @click="close">关闭</el-button></div>
    </el-dialog>
    <!-- ====预览页面结束==== -->

  </div>
</template>

<script>
// import { createAPI } from '@/utils/request.js'
import { mapActions } from 'vuex'
// import { update, remove } from '@/api/hmmm/articles'
import { provinces, citys } from '@/api/hmmm/citys'
import { remove, choiceAdd } from '@/api/hmmm/questions'
import { questionType, difficulty, direction } from '@/api/hmmm/constants'
// ================富文本编辑器设置
const toolbarOptions = [ // 工具栏想要显示什么，就要在这里加上什么
  ['bold', 'italic', 'underline', 'strike'], // 粗体、斜体、下划线、删除线
  [{ list: 'ordered' }, { list: 'bullet' }], // 有序列表、无序列表
  ['blockquote'],
  ['code-block', 'image', 'link'] // 链接、图片、视频
]

export default {
  data () {
    return {
      cities: ['上海', '北京', '广州', '深圳'],
      RadioisRight: '',
      checkRight: [],
      checkedCities: '',
      city: '',
      area: '',
      cityList: provinces(),
      areaList: [],
      pageSet: {
        page: 1,
        pageSize: 10
      }, // 分页器设置
      tableData: [], // 表单数据
      SubjectList: [], // 科目列表
      TagList: [], // 标签列表
      TwoList: [], // 二级目录列表
      createrList: [],
      questionType: questionType, // 题型列表
      difficulty: difficulty, // 难度列表
      direction: direction, // 方向列表
      subject: '',
      subjectID: '',
      TwoData: '',
      TagData: '',
      keyWord: '',
      questionTypeData: '',
      difficultyData: '',
      directionData: '',
      createrData: '',
      remarkData: '',
      companyData: '',
      radio: '',
      videoShow: false,
      QuestionDetail: [],
      // ====================================================
      title: '',
      state: '',
      open: false,
      viewShow: false,
      viewTitle: '',
      viewCreateTime: '',
      viewUserName: '',
      viewUserVisits: '',
      viewHtml: '',
      addShow: false,
      formTitle: '',
      articeId: '',
      formData: {
        title: '',
        articleBody: '',
        videoURL: ''
      },
      addRules: {
        title: [{ required: true, message: '请输入文章内容', trigger: 'blur' }],
        articleBody: [{ required: true, message: '请输入文章内容', trigger: 'blur' }]
      },
      options: {
        placeholder: '',
        modules: {
          toolbar: toolbarOptions
        }
      }
    }
  },
  async created () {
    // 获取基础题库列表
    await this.getQuestionListAsync(this.pageSet)
    this.tableData = this.$store.state.questionsChoice.questions.QuestionList
    // 学科列表
    await this.getSubjectListAsync({ pagesize: 1000 })
    this.SubjectList = this.$store.state.questionsChoice.questions.SubjectList
    // 获取录入人
    await this.getCreaterAsync()
    this.createrList = this.$store.state.questionsChoice.questions.createrList
    // console.log(this.createrList)
  },
  methods: {
    ...mapActions('questionsChoice/questions', ['getQuestionListAsync']),
    ...mapActions('questionsChoice/questions', ['getSubjectListAsync']),
    ...mapActions('questionsChoice/questions', ['getSubjectAsync']),
    ...mapActions('questionsChoice/questions', ['getTagAsync']),
    ...mapActions('questionsChoice/questions', ['getCreaterAsync']),
    ...mapActions('questionsChoice/questions', ['getQuestionDetailAsync']),
    // 获取二级目录
    async changeSubject (e) {
      this.subjectID = e
      this.TwoData = ''
      this.TagData = ''
      await this.getSubjectAsync(e)
      this.TwoList = this.$store.state.questionsChoice.questions.twoList
      await this.getTagAsync(e)
      this.TagList = this.$store.state.questionsChoice.questions.TagList
    },
    // 获取区级列表
    getAreaList (e) {
      this.area = ''
      this.areaList = citys(e)
    },
    // 重置表单
    clearForm () {
      this.subjectID = ''
      this.subject = ''
      this.TwoData = ''
      this.TagData = ''
      this.keyWord = ''
      this.questionTypeData = ''
      this.difficultyData = ''
      this.directionData = ''
      this.createrData = ''
      this.remarkData = ''
      this.companyData = ''
      this.city = ''
      this.area = ''
    },
    // 筛选
    async sumbit () {
      if (this.subjectID !== '' && this.keyWord !== '') {
        await this.getQuestionListAsync({ page: 1, pagesize: this.pageSet.pageSize, subjectID: this.subjectID, keyword: this.keyWord })
        this.tableData = this.$store.state.questionsChoice.questions.QuestionList
      } else if (this.subjectID === '' && this.keyWord !== '') {
        await this.getQuestionListAsync({ page: 1, pagesize: this.pageSet.pageSize, keyword: this.keyWord })
        this.tableData = this.$store.state.questionsChoice.questions.QuestionList
      } else if (this.subjectID !== '' && this.keyWord === '') {
        await this.getQuestionListAsync({ page: 1, pagesize: this.pageSet.pageSize, subjectID: this.subjectID })
        this.tableData = this.$store.state.questionsChoice.questions.QuestionList
      } else if (this.subjectID === '' && this.keyWord === '') {
        await this.getQuestionListAsync({ page: 1, pagesize: this.pageSet.pageSize })
        this.tableData = this.$store.state.questionsChoice.questions.QuestionList
      }
    }, // this.RadioisRight =
    // 预览功能
    async viewBox (id) {
      this.viewShow = true
      this.checkRight = []
      await this.getQuestionDetailAsync({ id: id })
      this.QuestionDetail = this.$store.state.questionsChoice.questions.QuestionDetail
      this.QuestionDetail.options.forEach(item => {
        if (item.isRight) {
          this.RadioisRight = item.code
          this.checkRight.push(item.code)
        }
      })
    },
    // 是否能打开视频
    video () {
      if (this.QuestionDetail.videoURL) {
        this.videoShow = true
      } else {
        this.$message('本题目暂无视频')
      }
    },
    close () {
      this.viewShow = false
      this.checkRight = []
    },
    // 删除功能
    deleteQuestion (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await remove({ id: id })
        await this.getQuestionListAsync(this.pageSet)
        this.tableData = this.$store.state.questionsChoice.questions.QuestionList
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
      })
    },
    // 加入精选功能
    joinChoice (id) {
      this.$confirm('此操作将该题目加入精选, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await choiceAdd({ id: id, choiceState: 1 })
        // ================跳转至精选
        this.$router.push('choice')
        this.$message({
          type: 'success',
          message: '添加成功!'
        })
      }).catch(() => {

      })
    },
    // ===========================================================================================================
    // ======更改每页个数
    async handleSizeChange (val) {
      this.pageSet.page = 1
      this.pageSet.pageSize = val
      await this.getQuestionListAsync({ page: this.pageSet.page, pagesize: val })
      this.tableData = this.$store.state.questionsChoice.questions.QuestionList
    },
    // ======更改页数=========
    async handleCurrentChange (val) {
      this.pageSet.page = val
      await this.getQuestionListAsync({ page: val, pagesize: this.pageSet.pageSize })
      this.tableData = this.$store.state.questionsChoice.questions.QuestionList
    }
  }
}
</script>

